import React from "react";
import { Switch } from 'antd';
import {CgDarkMode} from "react-icons/cg"
import { useContext } from "react";
import { ImageContext } from "../App";

const Header = ({ children }) => {
  const { response, isLoading,theme,themeChange } = useContext(ImageContext);
  const onChange = (checked) => {
    if(checked)
    themeChange("dark")
    else
    themeChange("cupcake")    
  };
 
  return (
    <div className=" navbar bg-base-100">
      <div className="navbar-start">
        <div className="dropdown">
          <label tabIndex={0} className="btn btn-ghost lg:hidden">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-5 w-5"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M4 6h16M4 12h8m-8 6h16"
              />
            </svg>
          </label>
          <ul
            tabIndex={0}
            className="menu menu-compact dropdown-content mt-3 p-2 shadow bg-base-100 rounded-box w-52"
          >
            <li>
              <a >Explore</a>
            </li>
            <li>
              <a >Collection</a>
            </li>
            <li>
              <a>Community</a>
            </li>
          </ul>
        </div>
        <a className="btn btn-ghost normal-case text-xl ml-10 font-thin ">Image Gallery</a>
      </div>
      <div className="navbar-center hidden lg:flex">
        <ul className="menu menu-horizontal px-1"></ul>
        <div className="inline-flex justify-end pr-20">{children}</div>
        <ul className="flex px-2 justify-between">
          <li className="pr-10">Explore</li>
          <li className="pr-10">Collection</li>
          <li className="pr-10">Community</li>
        </ul>
      </div>
      <div className="navbar-end mr-10">
        <div className="px-2">
      <CgDarkMode className="text-2xl"/>     
          </div>   
        <Switch onChange={onChange} />
      </div>
    </div>
  );
};

export default Header;
